import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;

import Vue from 'vue'
import App from './App'
import store from './store'

//导入封装的请求对象
import api from './api/index.js'

Vue.prototype.$store = store
// 将请求对象设置为全局属性
Vue.prototype.$api = api

Vue.config.productionTip = false

App.mpType = 'app'

const app = new Vue({
	store,
    ...App
})
app.$mount()